<template>
    <div class="prediction">
      <div class="bg"> 
        <div class="title">{{$store.state.language=='CN'?'模拟测算':'Simulation calculation'}}</div>
        <div class="body">
          <div class="divinput">
            <div class="divInputTitle">{{$store.state.language=='CN'?'购买金额:':'purchasing price:'}}</div>
            <div class="divInputdata"><input type="text" name="" id="" :value="mockBetting.mockBettingAmount" @blur="inputvalueChange"><span>ETH</span></div>
          </div>
          <div class="divbtn">
            <div class="btnItem" @click="clickMockBetting_true">{{$store.state.language=='CN'?'模拟一下':'Simulate'}}</div>
            <div class="btnItem" @click="clickMockBetting_false">{{$store.state.language=='CN'?'我的数据':'My data'}}</div>
          </div>
          <div class="item">
            <div class="key">&gt;&gt;&gt;</div>
            <div class="text">{{$store.state.language=='CN'?'在输入框中输入拟购买的金额（最大不超过 20.475ETH）':'Enter the amount  in the input box below (maximum 20.475ETH)'}}</div>
          </div>
          <div class="item">
            <div class="key">&gt;&gt;&gt;</div>
            <div class="text">{{$store.state.language=='CN'?'平台将为您模拟收益预测等各项数据，完成后查看我的数据仪表':'The platform will simulate various data such as income forecast for you, and check my data meter after completion'}}</div>
          </div>
          <div class="item">
            <div class="key">&gt;&gt;&gt;</div>
            <div class="text">{{$store.state.language=='CN'?'其中数据均为本轮结束后模拟情况，仅供参考':'The data are all simulated after the end of this round, for reference only'}}</div>
          </div>
          
          
        </div>
      </div>
    </div>
</template>

<script>
export default {
  data(){
    return {
      mockBetting:this.$store.state.mockBetting,

    }
  },
  methods:{
    ...mapActions([
      
      "upmockBetting",
    ]),
    clickMockBetting_true(){
      //upmockBetting
      //this.$store.state.mockBetting.isDisplay=true;
      this.upmockBetting({mockBetting:{
        mockBettingAmount:this.$store.state.mockBetting.mockBettingAmount,
        isDisplay:true,
      }})
      this.mockBetting.mockBettingAmount=this.$store.state.mockBetting.mockBettingAmount;
      this.mockBetting.isDisplay=true;
    },
    clickMockBetting_false(){
      //this.$store.state.mockBetting.isDisplay=false;
      this.upmockBetting({mockBetting:{
        mockBettingAmount:this.$store.state.mockBetting.mockBettingAmount,
        isDisplay:false,
      }})
      this.mockBetting.mockBettingAmount=this.$store.state.mockBetting.mockBettingAmount;
      this.mockBetting.isDisplay=false;
    },
    inputvalueChange(event){
      let tempvalue=isNaN(Number(event.target.value))?0:Number(event.target.value);
      if(tempvalue>20.475){
        tempvalue=20.475;
      }else if(tempvalue<0){
        tempvalue=0;
      }
      this.upmockBetting({mockBetting:{
        mockBettingAmount:tempvalue,
        isDisplay:this.$store.state.mockBetting.isDisplay,
      }})
      this.mockBetting.mockBettingAmount=tempvalue;
      this.mockBetting.isDisplay=this.$store.state.mockBetting.isDisplay;
      
    },
  }

}
import { mapActions } from "vuex";
</script>

