<template>
   <div class="m">
    <div class="r1">
      <div class="r1-line-g">
        <div
          class="r1-line"
          v-for="(item, index) of line"
          v-bind:key="item"
          :style="
            `transform: rotate(${225 +
              4.5 * index}deg) translate3d(0, -880%, 0) scale(1,${
              index % 10 ? 0.5 : 1
            });
            background: ${lineColor[index]};
            `
          "
        ></div>
        <div class="divtext">
          <div>{{$store.state.language=='CN'?'挖矿中。。。':'Mining. . .'}}</div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'总进度:':'total progress:'}}</span><span>{{getSchedule}}%</span></div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'会员数:':'Number of members:'}}</span><span>{{$store.state.newSageRouter.lastUserId}}</span></div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'购买额:':'Purchase amount:'}}</span><span>{{getPurchaseAmount}}</span></div>
        </div>
      </div>
      <canvas id="canvasDemo" ref="canvasDemo" width="250" height="250">
      </canvas>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      pper:0,
      pper_interal:null,
      line:61,
      lineColor:[],
    }
    
  },
  watch:{
    "$store.state.newSageRouter.totalAmounts"(){
       let that = this;
      let sageRouter=Number(that.$store.state.newSageRouter.totalAmounts)/
                      Number(that.$store.state.newSageRouter.threshold);
      if(this.$store.state.mockBetting.isDisplay){
        sageRouter = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(this.$store.state.newSageRouter.threshold);
      }
      sageRouter= sageRouter*100;
      if(sageRouter>100){
        sageRouter=100
      }    
      this.pper=0; 
      that.drawCanvanPercent("canvasDemo",sageRouter);
      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
    },
    "$store.state.mockBetting.mockBettingAmount"(){
       let that = this;
      let schedule = Number(this.$store.state.newSageRouter.totalAmounts)/
                      Number(this.$store.state.newSageRouter.threshold);
      if(this.$store.state.mockBetting.isDisplay){
        schedule = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(this.$store.state.newSageRouter.threshold);
      }
      schedule= schedule*100;
      if(schedule>100){
        schedule=100
      }    
      this.pper=0; 
      that.drawCanvanPercent("canvasDemo",schedule);
      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
    }
  },
  computed:{
    //进度
    getSchedule(){
      let schedule = Number(this.$store.state.newSageRouter.totalAmounts)/
                      Number(this.$store.state.newSageRouter.threshold)*100;
      if(this.$store.state.mockBetting.isDisplay){
        //this.$store.state.mockBetting.mockBettingAmount
        schedule = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(this.$store.state.newSageRouter.threshold)*100;
      }
      return schedule;
    },
    //购买额
    getPurchaseAmount(){
      let purchaseAmount = Number(this.$store.state.newSageRouter.totalAmounts);
      if(this.$store.state.mockBetting.isDisplay){
        purchaseAmount = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount));
      }
      return purchaseAmount;
    }
    //this.mockBetting
    
  },
  mounted() {
    this.pper1=0; 
    this.drawCanvanPercent("canvasDemo",0.1);
    this.lineColor = this.gradientColor("#0083FF", "#FFCD20", 60);
    
  },
  methods:{
    drawCanvanPercent(ele_id,sageRouter) {
    let canvas = this.$refs[ele_id];
    var circle = {
      r: 125,//半角
      per:sageRouter<1?1:sageRouter+16/100*sageRouter,//进度116 this.getSchedule
      lineWidth: 15,//宽度
    }
    if (canvas.getContext) {
      const that = this;
      var ctx = canvas.getContext("2d");
      this.pper_interal = setInterval(() => {
        that.drawMove(ctx, circle,ele_id);
      }, 10);
    }
  },
  drawMove(ctx, circle,ele_id) {
    let canvas = this.$refs[ele_id];
    var ctx2 = canvas.getContext("2d");
    var ctx3 = canvas.getContext("2d");
    ctx3.clearRect(0, 0, 500, 500);
    ctx2.clearRect(0, 0, 500, 500);
    ctx.clearRect(0, 0, 500, 500);
    ctx2.lineJoin = "round";
    ctx3.beginPath();
    ctx3.strokeStyle = "#ddd";
    var g = ctx.createLinearGradient(0, 0, 180, 0);
    g.addColorStop(0, "rgba(228,104,97,0)");
    g.addColorStop(1, "rgba(228,104,97,1)");
    ctx.strokeStyle = g;

    ctx3.lineWidth = circle.lineWidth;
    ctx3.arc(
      250 / 2,
      250 / 2,
      circle.r - circle.lineWidth / 2,
      Math.PI * 0.25,
      Math.PI * 0.75,
      true
    );
    ctx3.lineCap = "round";
    ctx3.stroke();

    if (this.pper >= circle.per) {
      this.pper = circle.per;
      clearTimeout(this.pper_interal);
    } else {
      this.pper++;
    }
    ctx.beginPath();
    var g2 = ctx.createLinearGradient(0, 0, 180, 0);
    g2.addColorStop(0, "#0083FF");
    g2.addColorStop(1, "#FFCD20");
    ctx.strokeStyle = g2;
    ctx.lineCap = "round";
    ctx.lineWidth = circle.lineWidth;
    ctx.arc(
      250 / 2,
      250 / 2,
      circle.r - circle.lineWidth / 2,
      Math.PI * 0.75,
      Math.PI * (((this.pper / 100) * 234) / 180 + 0.75),
      false
    );
    ctx.stroke();
  },
  // 将hex表示方式转换为rgb表示方式(这里返回rgb数组模式)
  colorRgb(sColor) {
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
     sColor = sColor.toLowerCase();
    if (sColor && reg.test(sColor)) {
      if (sColor.length === 4) {
        var sColorNew = "#";
        for (let i = 1; i < 4; i += 1) {
          sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
        }
        sColor = sColorNew;
      }
      //处理六位的颜色值
      var sColorChange = [];
      for (let i = 1; i < 7; i += 2) {
        sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
      }
      return sColorChange;
    } else {
      return sColor;
    }
  },
  colorHex(rgb) {
    var _this = rgb;
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
    if (/^(rgb|RGB)/.test(_this)) {
      var aColor = _this.replace(/(?:(|)|rgb|RGB)*/g, "").split(",");
      var strHex = "#";
      for (var i = 0; i < aColor.length; i++) {
        let hex = Number(aColor[i]).toString(16);
        hex = hex < 10 ? 0 + "" + hex : hex; // 保证每个rgb的值为2位
        if (hex === "0") {
          hex += hex;
        }
        strHex += hex;
      }
      if (strHex.length !== 7) {
        strHex = _this;
      }
      return strHex;
    } else if (reg.test(_this)) {
      var aNum = _this.replace(/#/, "").split("");
      if (aNum.length === 6) {
        return _this;
      } else if (aNum.length === 3) {
        var numHex = "#";
        for (let i = 0; i < aNum.length; i += 1) {
          numHex += aNum[i] + aNum[i];
        }
        return numHex;
      }
    } else {
      return _this;
    }
  },
  gradientColor(startColor, endColor, step) {
    let startRGB = this.colorRgb(startColor); //转换为rgb数组模式
    let startR = startRGB[0];
    let startG = startRGB[1];
    let startB = startRGB[2];

    let endRGB = this.colorRgb(endColor);
    let endR = endRGB[0];
    let endG = endRGB[1];
    let endB = endRGB[2];

    let sR = (endR - startR) / step; //总差值
    let sG = (endG - startG) / step;
    let sB = (endB - startB) / step;

    var colorArr = [];
    for (var i = 0; i < step; i++) {
      //计算每一步的hex值
      var hex = this.colorHex(
        "rgb(" +
          parseInt(sR * i + startR) +
          "," +
          parseInt(sG * i + startG) +
          "," +
          parseInt(sB * i + startB) +
          ")"
      );
      colorArr.push(hex);
    }
    return colorArr;
  },
  }
  
}
</script>

<style scoped>
