<template>
   <div class="m">
    <div class="r1">
      <div class="r1-line-g">
        <div
          class="r1-line"
          v-for="(item, index) of line"
          v-bind:key="item"
          :style="
            `transform: rotate(${225 +
              4.5 * index}deg) translate3d(0, -830%, 0) scale(1,${
              index % 10 ? 0.5 : 1
            });
            background: ${lineColor[index]};
            `
          "
        ></div>
        <div class="divtext">
          <div><span>{{$store.state.language=='CN'?'倒计时:':'Countdown:'}}</span><span>00:00:00</span></div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'投入:':'Betting:'}}</span><span>{{getInvestIn}} ETH</span></div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'收益:':'income:'}}</span><span>{{Number(getIncome).toFixed(3)}} ETH</span></div>
          <div class="divtextC"><span>{{$store.state.language=='CN'?'奖励:':'reward:'}}</span><span>{{Number(getReward).toFixed(3)}} NETO</span></div>
        </div>
      </div>
      <canvas  class="canvasDemo1" id="canvasDemo" ref="canvasDemo" width="250" height="250">
      </canvas>
      <canvas class="canvasDemo2" id="canvasDemo2" ref="canvasDemo2" width="250" height="250">
      </canvas>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      pper:0,
      pper1:0,
      pper2:0,
      pper_interal:null,
      line:61,
      lineColor:[],
      c:250/2
    }
    
  },
  computed:{
    //奖励
    getReward(){
      //挖矿收益率
      let miningYield = (Number(this.$store.state.newSageRouter.totalAmounts)/Number(this.$store.state.newSageRouter.threshold)*13.2/100);
      if(this.$store.state.mockBetting.isDisplay){
        miningYield = ((Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/Number(this.$store.state.newSageRouter.threshold)*13.2/100);
      }
      //末奖收益率
      let lastPrizeYield = (Number(this.$store.state.newSageRouter.threshold)*1.3*0.1*0.6)/((3+Number(this.$store.state.newSageRouter.threshold)/0.4*1.5)*
              (Number(this.$store.state.newSageRouter.threshold)/0.4*1.5-3+1)/2*0.75)*(this.$store.state.newSageRouter.perBetId+1+3)*0.75;

      let reward = Number(this.$store.state.matrixdata.users.incomeDeto);
      if(this.$store.state.mockBetting.isDisplay){
        reward = (Number(this.$store.state.matrixdata.users.incomeDeto)+Number(this.$store.state.mockBetting.mockBettingAmount)*(miningYield+lastPrizeYield));
      }

      return reward/(Number(this.$store.state.newSageRouter.DETOPrice)/Number(this.$store.state.newSageRouter.ETHPrice));
    },
    //收益
    getIncome(){
      let bettingYield = 0.06*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        Number(this.$store.state.matrixdata.users.betAmount)/20.475;
      if(this.$store.state.mockBetting.isDisplay){
        bettingYield = 0.06*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        (Number(this.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/20.475;
      }

      // let income = Number(this.$store.state.matrixdata.users.income);
      // if(this.$store.state.mockBetting.isDisplay){
      //   income = (Number(this.$store.state.matrixdata.users.income)+Number(this.$store.state.mockBetting.mockBettingAmount)*bettingYield);
      // }
      return bettingYield;
    },
     //挖矿收益率
    getMiningYield(){
      let miningYield = (Number(this.$store.state.newSageRouter.totalAmounts)/Number(this.$store.state.newSageRouter.threshold)*13.2/100).toFixed(2);
      if(this.$store.state.mockBetting.isDisplay){
        miningYield = ((Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/Number(this.$store.state.newSageRouter.threshold)*13.2/100).toFixed(2);
      }
      return miningYield;
    },
    //投注收益率
    getBettingYield(){
      let bettingYield = 0.05*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        Number(this.$store.state.matrixdata.users.betAmount)/20.475;
      if(this.$store.state.mockBetting.isDisplay){
        bettingYield = 0.05*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        (Number(this.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/20.475;
      }
      return bettingYield;
    },
    //末奖收益率
    getLastPrizeYield(){
      let lastPrizeYield = (Number(this.$store.state.newSageRouter.threshold)*1.3*0.22*0.6)/((3+Number(this.$store.state.newSageRouter.threshold)/0.4*1.5)*
              (Number(this.$store.state.newSageRouter.threshold)/0.4*1.5-3+1)/2*0.75)*(this.$store.state.newSageRouter.perBetId+1+3)*0.75;
      return lastPrizeYield;
    },
    //投入
    getInvestIn(){
      let investIn = Number(this.$store.state.matrixdata.users.betAmount);
      if(this.$store.state.mockBetting.isDisplay){
        investIn = Number(this.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount);
      }
      return investIn;
    },
    //进度
    getSchedule(){
      let schedule = Number(this.$store.state.newSageRouter.totalAmounts)/
                      Number(this.$store.state.newSageRouter.threshold)*100;
      if(this.$store.state.mockBetting.isDisplay){
        //this.$store.state.mockBetting.mockBettingAmount
        schedule = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(this.$store.state.newSageRouter.threshold)*100;
      }
      return schedule;
    },
    //购买额
    getPurchaseAmount(){
      let purchaseAmount = Number(this.$store.state.newSageRouter.totalAmounts);
      if(this.$store.state.mockBetting.isDisplay){
        purchaseAmount = (Number(this.$store.state.newSageRouter.totalAmounts)+Number(this.$store.state.mockBetting.mockBettingAmount));
      }
      return purchaseAmount;
    }
    //this.mockBetting
    
  },
  watch:{
    "$store.state.matrixdata.users.betAmount"(){
       let that = this;
      let betAmountsageRouter=Number(that.$store.state.matrixdata.users.betAmount)/
                      Number(20.475);
      if(this.$store.state.mockBetting.isDisplay){
        betAmountsageRouter = (Number(that.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(20.475);
      }
      betAmountsageRouter= betAmountsageRouter*100;
      if(betAmountsageRouter>100){
        betAmountsageRouter=100
      } 
      this.pper1=0;
      that.drawCanvanPercent("canvasDemo",betAmountsageRouter);
      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
    },
    "$store.state.matrixdata.users.income"(){
      let that = this;
      
      let incomesageRouter=Number(that.$store.state.matrixdata.users.income)/
                      Number(20.475);
      if(this.$store.state.mockBetting.isDisplay){
        incomesageRouter = (0.06*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        (Number(this.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/20.475)/Number(20.475);
      }  
      incomesageRouter= incomesageRouter*100;
      if(incomesageRouter>100){
        incomesageRouter=100
      }     
      this.pper2=0;        
      that.drawCanvanPercent("canvasDemo2",100);
      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
     },
     "$store.state.mockBetting.mockBettingAmount"(){

       let that = this;
       
      let betAmountsageRouter=Number(that.$store.state.matrixdata.users.betAmount)/
                      Number(20.475);
      if(this.$store.state.mockBetting.isDisplay){
        betAmountsageRouter = (Number(that.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/
                      Number(20.475);
      }
      betAmountsageRouter= betAmountsageRouter*100;
      if(betAmountsageRouter>100){
        betAmountsageRouter=100
      } 
       this.pper1=0;
      that.drawCanvanPercent("canvasDemo",betAmountsageRouter);
      

      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
      let incomesageRouter=Number(that.$store.state.matrixdata.users.income)/
                      Number(20.475);
      if(this.$store.state.mockBetting.isDisplay){
        incomesageRouter = (0.06*(1-Number(this.$store.state.newSageRouter.lastUserId)/(Number(this.$store.state.newSageRouter.threshold)/0.4))*Number(this.$store.state.newSageRouter.threshold)*1.1*0.67*
                        (Number(this.$store.state.matrixdata.users.betAmount)+Number(this.$store.state.mockBetting.mockBettingAmount))/20.475)/Number(20.475);
      }    
      incomesageRouter= incomesageRouter*100;
      if(incomesageRouter>100){
        incomesageRouter=100
      }    
       this.pper2=0;
      that.drawCanvanPercent("canvasDemo2",incomesageRouter);
      that.lineColor = that.gradientColor("#0083FF", "#FFCD20", 60);
     }
  },
  mounted() {
     
    this.drawCanvanPercent("canvasDemo",0.1);
    this.drawCanvanPercent("canvasDemo2",0.1);
    this.lineColor = this.gradientColor("#0083FF", "#FFCD20", 60);
    
  },
  methods:{
    drawCanvanPercent(ele_id,sageRouter) {
    let canvas = this.$refs[ele_id];
    var circle = {
      r: 125,//半角
      per:sageRouter<1?1:sageRouter+16/100*sageRouter,//进度
      lineWidth: 15,//宽度
    }
    if (canvas.getContext) {
      const that = this;
      var ctx = canvas.getContext("2d");
      // ctx.clearRect(0,0,canvas.width,canvas.height); 
      if(ele_id=='canvasDemo'){
        this.pper_interal1 = setInterval(() => {
          that.drawMove(ctx, circle,ele_id);
        }, 10);
      }else{
        this.pper_interal2 = setInterval(() => {
          that.drawMove(ctx, circle,ele_id);
        }, 10);
      }
    }
  },
  drawMove(ctx, circle,ele_id) {
    let that = this;
    let canvas = this.$refs[ele_id];
    if(!canvas){
      clearTimeout(this.pper_interal);
      return;
    }
    var ctx2 = canvas.getContext("2d");
    var ctx3 = canvas.getContext("2d");
    ctx3.clearRect(0, 0, 500, 500);
    ctx2.clearRect(0, 0, 500, 500);
    ctx.clearRect(0, 0, 500, 500);
    ctx2.lineJoin = "round";
    ctx3.beginPath();
    ctx3.strokeStyle = "#ddd";
    var g = ctx.createLinearGradient(0, 0, 180, 0);
    g.addColorStop(0, "rgba(228,104,97,0)");
    g.addColorStop(1, "rgba(228,104,97,1)");
    ctx.strokeStyle = g;

    ctx3.lineWidth = circle.lineWidth;
    ctx3.arc(
      that.c,
      that.c,
      circle.r - circle.lineWidth / 2,
      Math.PI * 0.25,
      Math.PI * 0.75,
      true
    );
    ctx3.lineCap = "round";
    ctx3.stroke();
    if(ele_id=='canvasDemo'){
      if (this.pper1 >= circle.per) {
        this.pper1 = circle.per;
        clearTimeout(this.pper_interal1);
      } else {
        this.pper1++;
      }
    }else{
      if (this.pper2 >= circle.per) {
        this.pper2 = circle.per;
        clearTimeout(this.pper_interal2);
      } else {
        this.pper2++;
      }
    }
    
    ctx.beginPath();
    var g2 = ctx.createLinearGradient(0, 0, 180, 0);
    g2.addColorStop(0, "#0083FF");
    g2.addColorStop(1, "#FFCD20");
    ctx.strokeStyle = g2;
    ctx.lineCap = "round";
    ctx.lineWidth = circle.lineWidth;
    if(ele_id=='canvasDemo'){
ctx.arc(
      that.c,
      that.c,
      circle.r - circle.lineWidth / 2,
      Math.PI * 0.75,
      Math.PI * (((this.pper1 / 100) * 234) / 180 + 0.75),
      false
    );
    }else{
      ctx.arc(
      that.c,
      that.c,
      circle.r - circle.lineWidth / 2,
      Math.PI * 0.75,
      Math.PI * (((this.pper2 / 100) * 234) / 180 + 0.75),
      false
    );
    }
    
    ctx.stroke();
  },
  // 将hex表示方式转换为rgb表示方式(这里返回rgb数组模式)
  colorRgb(sColor) {
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
     sColor = sColor.toLowerCase();
    if (sColor && reg.test(sColor)) {
      if (sColor.length === 4) {
        var sColorNew = "#";
        for (let i = 1; i < 4; i += 1) {
          sColorNew += sColor.slice(i, i + 1).concat(sColor.slice(i, i + 1));
        }
        sColor = sColorNew;
      }
      //处理六位的颜色值
      var sColorChange = [];
      for (let i = 1; i < 7; i += 2) {
        sColorChange.push(parseInt("0x" + sColor.slice(i, i + 2)));
      }
      return sColorChange;
    } else {
      return sColor;
    }
  },
  colorHex(rgb) {
    var _this = rgb;
    var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
    if (/^(rgb|RGB)/.test(_this)) {
      var aColor = _this.replace(/(?:(|)|rgb|RGB)*/g, "").split(",");
      var strHex = "#";
      for (var i = 0; i < aColor.length; i++) {
        let hex = Number(aColor[i]).toString(16);
        hex = hex < 10 ? 0 + "" + hex : hex; // 保证每个rgb的值为2位
        if (hex === "0") {
          hex += hex;
        }
        strHex += hex;
      }
      if (strHex.length !== 7) {
        strHex = _this;
      }
      return strHex;
    } else if (reg.test(_this)) {
      var aNum = _this.replace(/#/, "").split("");
      if (aNum.length === 6) {
        return _this;
      } else if (aNum.length === 3) {
        var numHex = "#";
        for (let i = 0; i < aNum.length; i += 1) {
          numHex += aNum[i] + aNum[i];
        }
        return numHex;
      }
    } else {
      return _this;
    }
  },
  gradientColor(startColor, endColor, step) {
    let startRGB = this.colorRgb(startColor); //转换为rgb数组模式
    let startR = startRGB[0];
    let startG = startRGB[1];
    let startB = startRGB[2];

    let endRGB = this.colorRgb(endColor);
    let endR = endRGB[0];
    let endG = endRGB[1];
    let endB = endRGB[2];

    let sR = (endR - startR) / step; //总差值
    let sG = (endG - startG) / step;
    let sB = (endB - startB) / step;

    var colorArr = [];
    for (var i = 0; i < step; i++) {
      //计算每一步的hex值
      var hex = this.colorHex(
        "rgb(" +
          parseInt(sR * i + startR) +
          "," +
          parseInt(sG * i + startG) +
          "," +
          parseInt(sB * i + startB) +
          ")"
      );
      colorArr.push(hex);
    }
    return colorArr;
  },
  }
  
}
</script>

